import {Injectable} from '@angular/core';
import {FlashMessageService, FlashMessageTypes} from './flash-message.service';
import {TasksLoaderManagerService} from '../tasks-loader-manager/ts/tasks-loader-manager.service';
//import {Http, Headers} from 'angular2/http';
//import 'rxjs/add/operator/map';

// Interface PostResponse (template for post response)
export interface PostResponse {
    status?: number,
    data?: any,
    errors?: any,
    flashMessages?: any
}


// Service
@Injectable()
export class PostService {
    constructor(
        private _flashMessageService: FlashMessageService,
        protected _tasksLoaderManagerService: TasksLoaderManagerService
    ) {}

    /**
     * Post. Send data to server
     * @param url
     * @param data
     * @returns {Promise}
     */
    public post(url: string, data: any = null): Promise<any>
    {
        this._tasksLoaderManagerService.addTask('POSTING_DATA');

        let that = this;

        return new Promise(function(resolve, reject) {
            return $.post(
                url,
                data,
                postResponse => {
                    // Unknown response, generally html responses (debug, exceptions, etc.)
                    if (!postResponse || (typeof postResponse !== 'object')) {
                        that._tasksLoaderManagerService.delTask('POSTING_DATA');
                        that.handleFlashMessages({});
                        return reject({});
                    }

                    // Regular response
                    that.handleFlashMessages(postResponse);
                    let isSuccess = (postResponse.status == 1);
                    delete postResponse.status; // Is no more necessary

                    // Success
                    if (isSuccess) {
                        that._tasksLoaderManagerService.delTask('POSTING_DATA');
                        return resolve(postResponse['data'] || null);
                    }

                    // Error
                    let errors = {
                        errors: (postResponse['errors'] || {}),
                        data: (postResponse['data'] || {})
                    };

                    that._tasksLoaderManagerService.delTask('POSTING_DATA');
                    return reject(errors);
                }
            ).fail(errors => {
                that._tasksLoaderManagerService.delTask('POSTING_DATA');
                that.handleFlashMessages({});
                return reject({});
            });
        });

        /*let headers = new Headers();
         headers.append('Content-Type', 'application/json');

         this._http.post(
         url,
         data,
         { headers: headers }
         )
         .map(response => response.json())
         .subscribe(
         response => console.log(response)
         );*/
    }

    /**
     * Handle handleFlashMessages from server
     * @param postResponse
     * @returns {PostResponse}
     */
    private handleFlashMessages(postResponse: PostResponse): any
    {
        // Request failed, no response has been returned.
        if (!('status' in postResponse)) {
            console.log(postResponse);
            this._flashMessageService.message(
                'Something went wrong, no response has been returned.',
                'Unknown error',
                FlashMessageTypes.error
            );
        }

        // Flash Messages
        if (postResponse.flashMessages) {
            for (let flashMessage of postResponse.flashMessages) {
                this._flashMessageService.message(
                    flashMessage.body,
                    flashMessage.head,
                    flashMessage.type
                );
            }
            delete postResponse.flashMessages; // It's no more necessary
        }

        return postResponse;
    }
}


